.self-protection {
    &__content {
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    &__bar {
        display: flex;
        align-items: center;
        padding: 0.4rem 0.8rem;
        position: relative;
        height: 1.6rem;
        border-radius: var(--border-radius-lg);
        background: linear-gradient(to right, var(--color-red-500) 0%, var(--color-orange-500) 25%, var(--color-yellow-500) 50%, var(--color-green-500) 75%);
    }
    &__point {
        height: 1.2rem;
        background-color: transparent;
        border-radius: 0;
        border-radius: var(--border-radius-lg);
        position: relative;
        &::after {
            border-radius: 50%;
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            width: 1.2rem;
            height: 1.2rem;
            background-color: #fff;
        }
    }
    &__description {
        display: grid;
        grid-template-columns: 1fr;
        gap: 0.8rem;
        h3 {
            font-size: var(--font-size-lg);
            color: var(--color-gray-500);
        }
        p {
            @extend %site-text;
        }
    }
}