.wave-quality {
    &__content {
        display: grid;
        grid-template-columns: 1fr;
        gap: 1.2rem;
    }
    &__image {
        width: 16rem;
        margin: 0 auto;
        img {
            max-width: 100%;
            display: block;
        }
    }
    &__text {
        @extend %site-text;
        text-align: center;
    }
}