.pulse-phase {
    &__content {
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    &__pulse-list {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
    }
    &__pulse {
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
        text {
            fill: #fff;
            font-weight: 700;
        }
        p {
            text-align: center;
            color: var(--color-gray-400);
            font-size: var(--font-size-sm);
        }
    }
    &__detail {
        h4 {
            text-align: center;
            color: var(--color-gray-400);
            font-size: var(--font-size-sm);
        }
    }
    &__detail-list {
        display: grid;
        grid-template-columns: 1fr;
    }
    &__btn {
        display: flex;
        justify-content: space-between;
        padding: 1.4rem 0;
        background-color: transparent;
        border: 0;
        border-bottom: solid 0.1rem var(--color-gray-75);
        span {
            color: var(--color-gray-500);
            font-size: var(--font-size-sm);
        }
    }
}