.body-type-detail,
.meridian-detail,
.tips-detail {
    &__content {
        display: grid;
        grid-template-columns: 1fr;
        gap: 1.2rem;
        & > h2 {
            font-size: var(--font-size-xl);
            color: var(--color-main-500);
            text-align: center;
            padding-top: 3.2rem;
        }
        & > ul {
            display: grid;
            grid-template-columns: 1fr;
            gap: 1.2rem;
            list-style: disc;
            padding-left: 1.2rem;
            li {
                @extend %site-text;
            }
        }
    }
}

.tips-detail {
    &__list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
        padding-top: 1.6rem;
    }
    &__item {
        display: flex;
        gap: 0.8rem;
        align-items: center;
        span {
            &:nth-child(1) {
                padding: 0.8rem;
                border-radius: var(--border-radius-sm);
                color: #fff;
                font-size: var(--font-size-sm);
            }
            &:nth-child(2) {
                font-size: var(--font-size-sm);
                color: var(--color-gray-500);
            }
        }
        &.red {
            span {
                &:nth-child(1) {
                    background-color: var(--color-red-500);
                }
            }
        }
        &.orange {
            span {
                &:nth-child(1) {
                    background-color: var(--color-orange-500);
                }
            }
        }
        &.yellow {
            span {
                &:nth-child(1) {
                    background-color: var(--color-yellow-500);
                }
            }
        }
        &.green {
            span {
                &:nth-child(1) {
                    background-color: var(--color-green-500);
                }
            }
        }
        &.wind {
            grid-column: 1 / 3;
            span {
                &:nth-child(1) {
                    background-color: var(--color-main-500);
                }
            }
        }
    }
}