.blood-oxygen {
    $self: &;
    &__content {
        display: flex;
        justify-content: center;
        padding: 1.2rem 0;
        svg {
            width: 16rem;
            text {
                fill: var(--color-main-500)
            }
            &.bad {
                text {
                    fill: var(--color-red-500)
                }
                #{$self}__circle {
                    stroke: var(--color-red-500);
                }
            }
        }
    }
    &__circle {
        stroke: var(--color-main-500);
        stroke-dasharray: 490 490;
        stroke-dashoffset: 0;
        stroke-linecap: round;
        transform-origin: 50%;
        transform: rotate(-90deg);
    }
}