.body-type {
    &__content {
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    &__type-image {
        width: 12rem;
        margin: 0 auto;
        img {
            max-width: 100%;
            display: block;
        }
    }
    &__description {
        h3 {
            font-size: var(--font-size-xl);
            text-align: center;
            color: var(--color-main-500);
            margin-bottom: 1.2rem;
        }
        p {
            @extend %site-text;
        }
    }
}