.measure-status-full {
    $self: &;
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
    &__item {
        display: grid;
        grid-template-columns: 1fr;
        position: relative;
        border-radius: var(--border-radius-lg);
        border: solid 0.1rem var(--color-gray-75);
        padding: 0;
        background-color: #fff;
        &.up {
            #{$self}__point {
                color: var(--color-green-500);
            }
        }
        &.down {
            #{$self}__point {
                color: var(--color-red-500);
            }
        }
    }
    &__info {
        display: grid;
        grid-template-columns: 1fr;
        gap: 0.4rem;
        padding-right: 1.2rem;
    }
    &__title {
        font-weight: 700;
        font-size: var(--font-size-xl);
        color: var(--color-gray-500);
    }
    &__text {
        @extend %site-text;
    }
    &__header {
        display: grid;
        grid-template-columns: 1fr 8rem;
        border-bottom: 0.1rem solid var(--color-gray-75);
        padding: 2.4rem 2.4rem 1.2rem;
    }
    &__detail {
        padding: 1.2rem 2.4rem 2rem;
    }
    &__btn-toggle {
        font-size: var(--font-size-base);
        color: var(--color-gray-400);
        border: 0;
        background-color: transparent;
        width: 100%;
        text-align: left;
        padding: 0;
        position: relative;
        &::before,
        &::after {
            width: 2rem;
            height: 2rem;
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            transform: translate3d(0,0.2rem,0);
            transition: opacity 300ms ease;
        }
        &::before {
            background: transparent url('../images/icon-open.svg') no-repeat 0 0;
            background-size: 2rem;
            opacity: 1;
        }
        &::after {
            background: transparent url('../images/icon-hide.svg') no-repeat 0 0;
            background-size: 2rem;
            opacity: 0;
        }
        &.active {
            &::before {
                opacity: 0;
            }
            &::after {
                opacity: 1;
            }
        }
    }
    &__point {
        font-size: var(--font-size-6xl);
        font-weight: 400;
        text-align: center;
        text-align: right;
        position: relative;
        transform: translate3d(0.8rem,0,0);
    }
    &__detail-content {
        display: none;
        padding-top: 0.8rem;
        p {
            @extend %site-text;
            margin-bottom: 0.8rem;
        }
    }
    &__ref {
        display: flex;
        gap: 0.8rem;
        span {
            border-radius: var(--border-radius-sm);
            padding: 0.4rem 0.8rem 0.4rem 2rem;
            font-weight: 700;
            color: #fff;
            position: relative;
            &::before {
                width: 0.8rem;
                height: 0.8rem;
                position: absolute;
                left: 0.8rem;
                top: 50%;
                transform: translate3d(0,-50%,0);
                background: transparent url('../images/icon-arrow-up.svg') no-repeat 0 0;
                background-size: 0.8rem;
                transform-origin: 50%;
                content: "";
            }
            &:nth-child(1) {
                background-color: var(--color-green-500);
            }
            &:nth-child(2) {
                background-color: var(--color-red-500);
                &::before {
                    transform: translate3d(0,-50%,0) rotate(-180deg);
                }
            }
        }
    }
}