.remark {
    &__content {
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    &__measure-tags {
        display: flex;
        gap: 0.8rem;
        span {
            border-radius: var(--border-radius-sm);
            border: solid 0.1rem var(--color-main-500);
            padding: 0.6rem;
            color: var(--color-main-500);
            font-size: var(--font-size-sm);
        }
    }
    &__remark-wrap {
        position: relative;
    }
    &__content-edit {
        position: relative;
        z-index: 10;
        font-size: var(--font-size-base);
        color: var(--color-gray-500);
        line-height: 1.6;
        background-color: var(--color-gray-50);
        padding: 1.6rem;
        border: solid 0.1rem var(--color-gray-75);
        border-radius: var(--border-radius-base);
        transition: all 300ms ease;
        min-height: 20rem;
        &:focus {
            outline: var(--color-main-500);
        }
    }
    &__counter {
        display: flex;
        gap: 0.4rem;
        position: absolute;
        right: 1.2rem;
        bottom: 1.2rem;
        z-index: 20;
        font-size: var(--font-size-sm);
        color: var(--color-gray-500);
    }
}