.rate-waveform {
    &__data {
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    &__rate,
    &__wave {
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    &__rate {
        border-bottom: 0.1rem solid var(--color-gray-75);
    }
    &__title {
        font-size: var(--font-size-base);
        color: var(--color-gray-400);
    }
    &__rate-value {
        text-align: center;
        color: var(--color-main-500);
        font-size: var(--font-size-7xl);
        padding-bottom: 2rem;
    }
}