.common-problem {
    &__content {
        display: grid;
        grid-template-columns: 1fr;
    }
    &__question {
        font-size: var(--font-size-lg);
        color: var(--color-red-500);
        font-weight: 700;
        padding: 1.2rem 4rem 1.2rem 0;
        border-bottom: 0.1rem solid var(--color-gray-75);
        cursor: pointer;
        position: relative;
        line-height: 1.4;
        &::after {
            content: "";
            width: 2.4rem;
            height: 2.4rem;
            position: absolute;
            right: 0;
            top: 1.2rem;
            transform: translate3d(0,0,0) rotate(180deg);
            background: transparent url('../images/icon-arrow.svg') no-repeat 0 0;
            background-color: cover;
            transition: all 300ms ease;
        }
        &.active {
            &::after {
                transform: translate3d(0,0,0) rotate(0);
            }
        }
    }
    &__answer {
        padding: 1.2rem 0;
        display: none;
        p {
            @extend %site-text;
            margin-bottom: 1.2rem;
        }
    }
    &__btn-buy {
        background-color: var(--color-green-500);
        display: block;
        border-radius: var(--border-radius-base);
        padding: 1.6rem;
        font-size: var(--font-size-base);
        color: #fff;
        position: relative;
        font-weight: 700;
        &::after {
            content: "";
            width: 2.4rem;
            height: 2.4rem;
            position: absolute;
            right: 1.6rem;
            top: 1.2rem;
            transform: translate3d(0,0,0);
            background: transparent url('../images/icon-cart.svg') no-repeat 0 0;
            background-color: cover;
        }
    }
}